import PropTypes from "prop-types"
import React from "react"
import Menu from '../components/menu'
import Link from 'gatsby-link'
import tutorials from '../images/tutorials.png';
import logo from '../images/logo.png';
import calendar from '../images/calendar.png';

require('bootstrap/dist/css/bootstrap.min.css')

const agendaLink = "https://calendar.google.com/calendar/embed?src=6ugi0o6fa4ekj0ti78k1pjdt3k%40group.calendar.google.com&ctz=Europe%2FParis";

const Header = () => (
    <header
        style={{

            marginBottom: `1.45rem`,
        }}
    >
        <Menu/>
        <div>

            <ul className="nav navbar-nav">
                <li>< Link to="/blog"><img src={tutorials} alt="logo"/> Tutorials </Link></li>
                <li className="navbar-brand">< Link to="/"><img src={logo} alt="logo"/></Link></li>
                <li>
                    < a rel="noopener noreferrer" href={agendaLink} target='_blank'>
                        <img src={calendar} alt="logo"/> Calendar
                    </a>
                </li>
            </ul>
        </div>
    </header>
)

Header.propTypes = {
    siteTitle: PropTypes.string,
}

Header.defaultProps = {
    siteTitle: ``,
}

export default Header
